define('js-week3-cr-qa/helpers/question-answer', ['exports', 'ember'], function (exports, _ember) {
  exports.questionAnswer = questionAnswer;

  function questionAnswer(params /*, hash*/) {
    var question = params[0];

    if (question.get('answers').get('length') >= 2) {
      return _ember['default'].String.htmlSafe('<span class="glyphicon glyphicon-fire"></span>');
    }
  }

  exports['default'] = _ember['default'].Helper.helper(questionAnswer);
});